.tue-breadcrumb-tpl {
    background: var(--color-orange-0);
    padding: 12px 16px;
    // position: sticky;
    // z-index: 8;
    // top: 64px;
    // @media (min-width: 992px) {
    //     top: 88px;
    // }
    .breadcrumb-content {        
        width: 100%;
        max-width: 1054px;
        margin: 0 auto;
    }

    .breadcrumb-list {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 16px;
        font-weight: 400;
        line-height: 1;

        .breadcrumb__item {
            line-height: 24px;
            +.breadcrumb__item {
                padding-left: 6px;

                &::before {
                    float: left;
                    padding-right: 6px;
                    color: var(--color-body);
                    content: url('data:image/svg+xml,<svg width="6" height="8" viewBox="0 0 6 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1.33984 7.33317L4.67318 3.99984L1.33984 0.666504" stroke="%23545353" stroke-linecap="round" stroke-linejoin="round"/></svg>');
                }
            }

            >a {
                color: var(--color-body);
                font-size: 16px;
                font-style: normal;
                font-weight: 500;                
            }

            &.active {
                color: var(--color-primary);
                font-size: 16px;
                font-style: normal;
                font-weight: 500;                
            }
        }
    }
}